@import url(https://fonts.googleapis.com/css?family=Open+Sans+Condensed:300,700|Open+Sans:400,300,600);
body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
.form-control label,
.form-control input,
.form-control textarea {
  width: 100%;
  display: block;
  overflow: hidden;
}
.form-control label {
  margin-bottom: 1rem;
}

.form-control {
  margin-bottom: 1rem;
}
.form-actions button,
.btn {
  background: #01d1d1;
  font: inherit;
  border: 1px solid #58a1a1;
  border-radius: 3px;
  padding: 0.25rem 0.6rem;
  margin-right: 0.6rem;
  box-shadow: 1px 1px 5px rgba(0, 0, 0, 0.26);
  color: white;
  cursor: pointer;
  pointer-events: auto;
}
.form-actions button:hover,
.form-actions button:active,
.btn:hover,
.btn:active {
  background: #01a7a7;
}

*{box-sizing:border-box}.log-form{width:40%;min-width:320px;max-width:475px;background:#fff;position:absolute;top:50%;left:50%;transform:translate(-50%, -50%);box-shadow:0px 2px 5px rgba(0,0,0,0.25)}@media (max-width: 40em){.log-form{width:95%;position:relative;margin:2.5% auto 0 auto;left:0%;transform:translate(0%, 0%)}}.log-form form{display:block;width:100%;padding:2em}.log-form h2{width:100%;color:#aaa;font-family:"open sans condensed";font-size:1.35em;display:block;background:#2a2a2a;width:100%;text-transform:uppercase;padding:0.75em 1em 0.75em 1.5em;box-shadow:inset 0px 1px 1px fadeout(white, 95%);border:1px solid #1d1d1d;margin:0;font-weight:200}.log-form input{display:block;margin:auto auto;width:100%;margin-bottom:2em;padding:0.5em 0;border:none;border-bottom:1px solid #eaeaea;padding-bottom:1.25em;color:#b3b0b0;font-size:1em}.log-form input:focus{outline:none}.log-form .btn{display:inline-block;border:1px solid #1ba0a9;padding:0.5em 2em;color:white;margin-right:0.5em;box-shadow:inset 0px 1px 0px fadeout(white, 80%)}.log-form .btn:hover{background:#23cad5}.log-form .btn:active{background:#1fb5bf;box-shadow:inset 0px 1px 1px fadeout(black, 90%)}.log-form .btn:focus{outline:none}.log-form .forgot{color:#33d3de;line-height:0.5em;position:relative;top:2.5em;text-decoration:none;font-size:0.75em;margin:0;padding:0;float:right}.log-form .forgot:hover{color:#1ba0a9}

.modal {
  overflow: hidden;
  position: absolute;
  width: 96%;
  background: white;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.26);
  position: fixed;
  top: 1vh;
  left: 5%;
  z-index: 100;
  opacity: 1;
}

.modal__header {
  padding: 1rem;
  background: #5101d1;
  color: white;
}

.modal__header h1 {
  margin: 0;
  font-size: 1.25rem;
}

.modal__content {
  padding: 1rem;
}

.modal__actions {
  display: flex;
  justify-content: flex-end;
  padding: 1rem;
}

@media (min-width: 768px) {
  .modal {
    width: 30rem;
    left: calc((100% - 30rem) / 2);
  }
}

.backdrop {
    position: fixed;
    top: 0;
    left: 0;
    height: 100vh;
    width: 100%;
    background: rgba(0, 0, 0, 0.75);
  }
