.card {
  position: relative;
  width: 300px;
  height: 400px;
  background: #262626;
  overflow: hidden;
  border-radius: 8px;
  margin-right: 30px;
}
.input,
.toggle {
  position: absolute;
  width: 50px;
  bottom: 20px;
  height: 50px;
  right: 20px;
  outline: none;
  z-index: 10;
}
.input {
  opacity: 0;
}
.toggle {
  pointer-events: none;
  border-radius: 50%;
  background: #fff;
  transition: 0.5s;
  text-align: center;
  line-height: 50px;
  font-size: 36px;
  box-shadow: 0 0 0 0px #9c27b0;
}
.input:checked ~ .toggle {
  box-shadow: 0 0 0 500px #20998d;
  transform: rotate(230deg);
}
#img {
  width: 300px;
  height: 400px;
}
.imgBx,
.content {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.content {
  padding: 20px;
  box-sizing: border-box;
  z-index: 11;
  pointer-events: none;
  transition: 0.5s;
  opacity: 0;
}
.input:checked ~ .content {
  opacity: 1;
  transition-delay: 0.5s;
}
.buttonsContainer {
  position: relative;
  top: 25%;
  display: flex;
  justify-content: space-between;
}

li {
  margin: 1rem 0;
}
.btnn {
  background: #01d1d1;
  font: inherit;
  border: 1px solid #58a1a1;
  border-radius: 7px;
  padding: 0.25rem 0.6rem;
  margin-right: 0.6rem;
  box-shadow: 1px 1px 5px rgba(0, 0, 0, 0.26);
  color: white;
  cursor: pointer;
  pointer-events: auto;
}

.btnn:hover,
.btnn:active {
