}

.event__list {
  position: relative;
  margin: 5rem 2rem 2rem 2rem;
  padding-bottom: 1rem;
  width: 20rem;
  max-width: 90%;
  list-style: none;

  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
}

.event__item {
}


.spinner {
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 10;
}
.lds-dual-ring {
  display: inline-block;
  width: 80px;
  height: 80px;
}
.lds-dual-ring:after {
  content: " ";
  display: block;
  width: 64px;
  height: 64px;
  margin: 8px;
  border-radius: 50%;
  border: 6px solid rgb(107, 100, 100);
  border-color: rgb(99, 101, 187) transparent rgb(224, 40, 40) transparent;
  animation: lds-dual-ring 1.2s linear infinite;
}
@keyframes lds-dual-ring {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

.bookings__list {
  list-style: none;
  margin: 10px auto;
  padding: 10px 10px;
  width: 40rem;
  max-width: 90%;
}

.bookings__item {
  /* margin: 0.5rem 0.5rem;
  padding: 0.5rem 0.5rem; */
  border: 1px solid #5101d1;
  justify-content: space-between;
  align-items: center;
  
}

.sp {
  display: flex;
  margin: 10px auto;
  padding: 10px 10px;
}

.main-navigation {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 3.5rem;
  background: #5101d1;
  padding: 0 2rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
  box-sizing: border-box;
}

.main-navigation__logo h1 {
  margin: 0;
  font-size: 1.5rem;
  color: #dfcefc;
}

.main-navigation__items {
  margin-left: 1.5rem;
}

.main-navigation__items ul {
  display: flex;
  list-style: none;
  padding: 0;
  margin: 0;
  align-items: center;
}

.main-navigation__items li {
  margin: 0 1rem;
}

.main-navigation__items a,
.main-navigation__items button {
  text-decoration: none;
  color: white;
  padding: 0.25rem 0.5rem;
  border: none;
  font: inherit;
  background: transparent;
  cursor: pointer;
  vertical-align: middle;
  margin: 0;
}

.main-navigation__items a:hover,
.main-navigation__items a:active,
.main-navigation__items a.active,
.main-navigation__items button:hover,
.main-navigation__items button:active {
  background: #ffffff;
  color: #5101d1;
  border-radius: 5px;
}

.main-content {
  margin: 4rem 2.5rem ;

